html, body {
  margin: 0;
  padding: 0;
  scroll-behavior: smooth;
}

header {
  height: 30px;
}

header h3 {
  margin-top: 0px;
}

header a {
  position: fixed;
  top: 0px;
  right: 15px;
}

#map {
  height: calc(100vh - 30px);
}

.leaflet-popup-content-wrapper {
  height: 300px;
  width: 300px;
}

.leaflet-pane, .leaflet-tile, .leaflet-marker-icon, .leaflet-marker-shadow, .leaflet-tile-container, .leaflet-pane > svg, .leaflet-pane > canvas, .leaflet-zoom-box, .leaflet-image-layer, .leaflet-layer {
  left: -20px;
}

#about {
  margin: 15px;
}

.inline {
  display: inline;
}

#revival {
  z-index: 400;
  position: absolute;
  top: 50px;
  left: 60px;
  height: 50px;
  width: 400px;
  background: white;
}

img.face {
  width: 25px;
}
